.pad {
padding: 5%;
}
ion-content{
    height: 100vh;
}
ion-label {
    font-family: 'DM Sans';
    font-weight: 400;
    font-size: 18px !important;
    line-height: 35px;
}
ion-list{
    background-color: #F3F5FF
}
ion-item{
    border: 2px solid #E5E5E5;
    margin: 5px 24px 5px 24px;
    border-radius: 15px;
}
.getStarted{
    font-size: x-large;
    font-weight: 400;
    background-color: #5E6DF2 !important;
    border-radius: 15px;
    color: whitesmoke;
    text-align: center;
}
